import React, { Fragment, useEffect, useState } from "react";
import { Row, Col, Form, Button } from "react-bootstrap";
import { getData } from "./actions/actions";
import { updateProductOrder, SendTodoAddOrder } from "./actions/sendTodo";

const initialState = {
    name: "",
    post: "",
    product_id: ""
};

const AddCategory = ({ history }) => {

    const [formData, setFormData] = useState(initialState);
    const [formData1, setFormData1] = useState(initialState);
    const [data, setData] = useState();
    

  const OnChange = (e) => {
    console.log(e.target);
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const sell = ()=> {

    updateProductOrder(formData, history.location.pathname.toString().slice(-1));
    formData.product_id = history.location.pathname.toString().slice(-1);
    formData.name = data.products.find(item => item.id == history.location.pathname.toString().slice(-1)).name;
    formData.price = data.products.find(item => item.id == history.location.pathname.toString().slice(-1)).price;
    SendTodoAddOrder(formData);
    console.log(formData);
    history.push("/")
  }


  const { name, count, seller_id, product_id} = formData;
  
 

  useEffect(() => {
    getData().then((result) => setData(result));
  }, [getData]);

  return data == null ? (
    <h1>loading</h1>
  ) : (
        <Fragment>
            <h1 className="text-center my-4">Оформление товара</h1>
                <Row className="justify-content-center">
                <Col className="col-md-5">
                <Form className="ml-3 mr-3">
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        
                        <Form.Label>Название товара: { data.products.find(item => item.id == history.location.pathname.toString().slice(-1)).name }</Form.Label>
                    </Form.Group>
                    
                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Label>Количество товара: { data.products.find(item => item.id == history.location.pathname.toString().slice(-1)).count } шт.</Form.Label>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Label>Цена товара:  { data.products.find(item => item.id == history.location.pathname.toString().slice(-1)).price } руб.</Form.Label>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Label>Продавец</Form.Label>
                    <Form.Control as="select" name="seller_id" onChange={OnChange}  value={seller_id}>
                    {data.sellers.map((item) => (
                        <option
                        as="button"
                        id={seller_id}
                        value={item.id}
                        key={item.id}
                        >
                        {item.name + " " + item.surname + " " + item.post}
                        </option>
                    ))}
                    </Form.Control>
                    <Form.Text className="text-muted">
                        Укажите себя
                    </Form.Text>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Укажите количество покупаемого товара</Form.Label>
                        <Form.Control placeholder="шт."  type="text" name="count" value={count} onChange={OnChange}/>
                    </Form.Group>
                    <Button className="mb-3" onClick={sell} variant="outline-success">
                        Продать
                    </Button>
                </Form>
                </Col>
                </Row>
        </Fragment>
    );
};

export default AddCategory;